import './App.scss';
import '@fontsource/roboto';

import React, { useEffect } from 'react';
import { Navigate, Route, Routes } from 'react-router-dom';

import { useAppDispatch, useAppSelector } from '../../hooks/redux';
import { auth } from '../../store/reducers/ActionCreators';
import { Login } from '../Authorization/Login';
import { Registration } from '../Authorization/Registration';
import CreateTask from '../CreateTask/CreateTask';
import { Header } from '../Header/Header';
import { Task } from '../Task/Task';
import { AllTaskList } from '../TaskList/AllTaskList';
import { TasksListByRole } from '../TaskList/TasksListByRole';

export const App = () => {
  const dispatch = useAppDispatch();
  const { isAuth } = useAppSelector((state) => state.storeReducer);

  useEffect(() => {
    dispatch(auth());
  }, []);

  return (
    <>
      {isAuth && <Header />}
      <main className="main__wrapper">
        {!isAuth ? (
          <Routes>
            <Route path="https://apknastashka.github.io/login" element={<Login />} />
            <Route path="https://apknastashka.github.io/registration" element={<Registration />} />
            <Route
              path="*"
              element={<Navigate to="https://apknastashka.github.io/login" replace />}
            />
          </Routes>
        ) : (
          <Routes>
            <Route path="https://apknastashka.github.io/" element={<TasksListByRole />} />
            <Route path="https://apknastashka.github.io/all" element={<AllTaskList />} />
            <Route path="https://apknastashka.github.io/create" element={<CreateTask />} />
            <Route path="https://apknastashka.github.io/task/:id" element={<Task />} />
            <Route path="*" element={<Navigate to="https://apknastashka.github.io/" replace />} />
          </Routes>
        )}
      </main>
    </>
  );
};
